import anime from 'animejs';

export function translate(element) {
    element.innerHTML = element.textContent.replace(/(\w)/g, "<span class='letter'>$&</span>");
    anime.timeline({loop: false})
    .add({
      targets: '.ml11 .line',
      scaleY: [0,1],
      opacity: [0.5,0],
      easing: "easeOutExpo",
      duration: 700
    })
    .add({
      targets: '.ml11 .line',
      opacity: [0.5,1],
      translateX: [0, document.querySelector('.ml11 .letters').getBoundingClientRect().width + 10],
      easing: "easeOutExpo",
      duration: 700,
      delay: 100
    }).add({
      targets: '.ml11 .letter',
      opacity: [0,1],
      easing: "easeOutExpo",
      duration: 600,
      offset: '-=775',
      delay: (el, i) => 34 * (i+1)
    }).add({
      targets: '.line',
      opacity: 0,
      duration: 700,
      easing: "easeOutExpo",
      delay: 500
    });
}